import React from 'react'
import styles from './Signup.css'
import axios from 'axios'

function Signup(props) {
    // class -> className
    // onclick -> onClick
    //  
    //  -> {} jsx 

    // 1.setmember    [ ]
    const setmember = () => {
        let info = {
                memail : document.querySelector('.memail').value ,
                mpassword : document.querySelector('.mpassword').value ,
                mphone : document.querySelector('.mphone').value
            }

        //   [ ajax ]
        //           [ fetch(react) ]
        //           [ axios(react)    ]
        //  axios 
        // npm :  / [ node.js ]
        // [   ] npm install axios
        // import axios from 'axios'

        // axios   [spring] 
        // @CrossOrigin(origins = "http://localhost:3000")
        // axios.MethodType("URL " , data) //    
        // axios.post() ,axios.get() ,axios.put() ,axios.delete()
        axios
            .post("http://localhost:8080/member/setmember" , info)  // 
            .then( res => { alert(res.data) } )                       //  
     }
    const getauth = () => { alert('  getauth')  }
    const setimer = () => { alert('  setimer')  }
    const authcode = () => { alert('  authcode')  }


    return(
            <div>
                <h3>  </h3>

                <div>
                     : <input type="text" className="memail"/>
                    <button type="button" onClick={setmember} className="getauthbtn"></button><br/>
                    <div className="authbox">
                        <input type="text" className="authinput"/>
                        <button type="button" className="authbtn" onClick={setmember}></button>
                        <span className="timerbox"></span>
                    </div>
                </div>
                 : <input type="text" className="mphone"/> <br/>
                 : <input type="text" className="mpassword"/> <br/>
                <button type="button" onClick={setmember}></button>
            </div>

    );
}

export default Signup;